@import url(https://fonts.googleapis.com/css?family=Oswald:400,500,600);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500);
@import url(https://fonts.googleapis.com/css2?family=Glory&display=swap);
body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #777;
    font-family: "Roboto", sans-serif;
    font-size: 15px;
    font-weight: 400;
    line-height: 1.667;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

a {
    text-decoration: none;
}

:root {
    --blue: #007bff;
    --indigo: #6610f2;
    --purple: #6f42c1;
    --pink: #e83e8c;
    --red: #dc3545;
    --orange: #fd7e14;
    --yellow: #ffc107;
    --green: #28a745;
    --teal: #20c997;
    --cyan: #17a2b8;
    --white: #fff;
    --gray: #6c757d;
    --gray-dark: #343a40;
    --primary: #007bff;
    --secondary: #6c757d;
    --success: #28a745;
    --info: #17a2b8;
    --warning: #ffc107;
    --danger: #dc3545;
    --light: #f8f9fa;
    --dark: #343a40;
    --breakpoint-xs: 0;
    --breakpoint-sm: 576px;
    --breakpoint-md: 768px;
    --breakpoint-lg: 992px;
    --breakpoint-xl: 1200px;
    --font-family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI",
        Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
        "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas,
        "Liberation Mono", "Courier New", monospace;
}
.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.footer {
    padding: 50px 0 20px 0;
    background-color: #35404f;
    color: #878c94;
    display: flex;
    align-items: center;
    justify-content: center;
}
.footer .title {
    text-align: left;
    color: #fff;
    font-size: 25px;
}
.footer__row {
    display: flex;
    justify-content: space-between;
}

.footer .about-div {
    text-align: left;
}

.footer .payment-div .title,
p {
    display: flex;
    justify-content: left;
}

.footer .payment {
    margin: 0px;
    padding: 0px;
    list-style-type: none;
}
.footer .payment li {
    list-style-type: none;
}
.footer .payment li a {
    text-decoration: none;
    color: #fff;
    float: left;
    font-size: 25px;
    padding: 10px 0px;
}
.footer__buy_with_meta_mask {
    width: 240px;
    padding: 10px 20px;
    background-color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.2s ease;
    opacity: 0.8;
}

.footer__buy_with_meta_mask img {
    width: 100%;
}
.footer__buy_with_meta_mask:hover {
    opacity: 1;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}

.payment-div {
    width: 25%;
}

.footer .text-center {
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 1054px) {
    .payment-div {
        width: 50%;
    }
}
@media screen and (min-width: 1221px) {
    .footer__container {
        max-width: 1200px;
        padding: 0px 40px;
    }
}

.header {
    position: fixed;
    width: 100%;
    opacity: 0.9;
    z-index: 10;
}
.dummy-head {
    width: 100%;
    height: 80px;
}
.header__brand__img {
    height: 50px;
    margin: 3px 20px;
}

.header-wrapper {
    display: flex;
    background-color: #f8f9fa;
    justify-content: center;
}
.navbar {
    width: 100%;
    max-width: 1200px;
}
.navbar-brand {
    color: black;
    font-size: large;
    letter-spacing: 0.05rem;
    font-weight: 900;
}


.navbar-collapse{
    justify-content: right;
}

.nav_links a{
    color: black;
    font-size:large;
    letter-spacing: 0.05rem;
    font-weight: 900;
    /* padding: 0.5rem 1.5rem; */
    margin: 0 1rem;
    

    /* text-transform: uppercase; */
}

.hospitals__main-title {
    text-align: center;
    padding: 10px;
    padding-top: 30px;
    /* color: #0d6efd; */
    color: #8cbaff; /*temp*/
    display: flex;
    justify-content: center;
    align-items: center;
}
.hospitals-wrapper {
    display: flex;
    justify-content: center;
}
.hospitals {
    width: 100%;
    max-width: 1200px;
}
.hospitals__main-title:hover {
    transition: 0.3s;
}

.hospitalsTitle div {
    width: 10px;
    height: 10px;
    background-color: red;
}
.hospitals__hospitals-container {
    display: flex;
    flex-wrap: wrap;
}
.hospitals__hospitals-container__card {
    width: 18rem;
    color: black;
    margin: 20px;
    transition: all 0.2s ease;
}

.hospitals__hospitals-container__card:hover {
    transition: 0.3s;
    /* transform: translateX(10px); */
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    /* opacity: 0.7; */
}
.hospitals__hospitals-container__card__card-body {
    display: flex;
    align-items: center;
    flex-direction: column;
}
.hospitals__hospitals-container__card__card-body h5 {
    font-family: "Oswald", sans-serif;
}
.hospitals__hospitals-container__card__card-body p,
h6 {
    color: #777;
    font-family: "Roboto", sans-serif;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-align: center;
    text-overflow: ellipsis;
}

.hospitaldetail__back-button {
    width: 50px;
    height: 50px;
    display: flex;
    margin: 10px;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.904);
    color: black;
    z-index: 1;
    border-radius: 2px;
    cursor: pointer;
    position: fixed;
    transition: all 0.2s ease;
}
.hospitaldetail__back-button:hover {
    background-color: rgb(221, 221, 221);
}
.hospitaldetail__hospital-detail-area {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.hospitaldetail__hospital-detail-area__container {
    display: flex;
    width: 100%;
    max-width: 1200px;
    padding: 20px;
}
.hospitaldetail__hospital-detail-area__container__image {
    /* max-width: 500px;/ */
    flex: 0.5 1;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.hospitaldetail__hospital-detail-area__container__image img {
    width: 100%;
}
.hospitaldetail__hospital-detail-area__container__hospitals-text {
    flex: 0.5 1;
    /* width: 100%; */
    padding: 5rem 3rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.hospitaldetail__hospital-detail-area__container__hospitals-text h3 {
    font-family: "Oswald", sans-serif;
    font-size: 24px;
    font-weight: 500;
    color: #222;
    margin-bottom: 20px;
    line-height: 1.1;
}

.hospitaldetail__hospital-detail-area__container__hospitals-text h2 {
    font-family: "Roboto", sans-serif;
    font-size: 24px;
    font-weight: 700;
    color: #384aeb;
    margin-bottom: 10px;
}

.hospitaldetail__hospital-detail-area__container__hospitals-text h2 {
    padding-top: 20px;
    border-top: 1px dotted #eeeeee;
    border-bottom: 1px dotted #eeeeee;
    margin-top: 20px;
    padding-bottom: 26px;
    margin-bottom: 0;
}
.buy-button {
    padding: 10px 20px;
}
@media screen and (max-width: 991px) {
    .hospitaldetail__hospital-detail-area__container {
        flex-direction: column;
    }
    .hospitaldetail__hospital-detail-area__container__image {
        width: 100%;
        padding: 20px 50px;
        max-width: 990px;
    }
}

/*---------- Ratings --------*/
.hospitaldetail__hospital-review-area {
    width: 100%;
    display: flex;
    justify-content: center;
}
.hospitaldetail__hospital-review-area__container {
    width: 100%;
    max-width: 1200px;
    padding: 20px;
}
.hospitaldetail__hospital-review-area__container__content {
    display: flex;
    flex-direction: column;
}
.hospitaldetail__hospital-review-area__container__content__reviews {
    flex: 0.5 1;
}
.hospitaldetail__hospital-review-area__container__content__create-review-box {
    flex: 0.5 1;
}
.hospitaldetail__hospital-review-area__container__content__reviews__overallreview {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 90px;
}
.overallreview-container {
    width: 100%;
    height: 100%;
    background: #f1f6f7;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    font-family: "Oswald", sans-serif;
}
.overallreview-container h5 {
    color: #333333;
    margin-bottom: 0px;
    font-size: 24px;
    line-height: 1.1;
}
.overallreview-container h4 {
    font-size: 48px;
    font-weight: bold;
    color: #384aeb;
    line-height: 1.3;
}
.overallreview-container h6 {
    font-family: "Roboto", sans-serif;
    margin-bottom: 0px;
    font-size: 14px;
    color: #555555;
    font-weight: normal;
}

/* .hospitaldetail__hospital-review-area__container__content__reviews__all-reviews {
    overflow-y: scroll;
  } */

.hospitaldetail__hospital-review-area__container__content__reviews__all-reviews__review-card {
    color: #777;
    font-family: "Roboto", sans-serif;
    font-size: 15px;
    font-weight: 400;
    line-height: 1.667;
    display: flex;
    flex-direction: column;
}
.review-card__name_details {
    display: flex;
    width: 100%;
    padding: 10px;
}
.review-card__name_details__image {
    width: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.review-card__name_details__image img {
    width: 100%;
    border-radius: 50%;
}
.review-card__name_details__name {
    font-family: "Oswald", sans-serif;
    color: black;
    width: 100%;
    padding: 5px 10px;
    line-height: 1.3;
}

.review-card__name_details__name__date {
    padding-left: 30px;
}
.review-card__review-text {
    padding: 20px;
}
.set-stars {
    cursor: pointer;
}
.set-stars:hover {
    color: #fb9700;
}
.yellow {
    color: #fbd600;
}
.hospitaldetail__hospital-review-area__container__content__create-review-box {
    padding-left: 20px;
}
.hospitaldetail__hospital-review-area__container__content__create-review-box
    h4 {
    font-size: 20px;
    color: #222;
    margin-bottom: 20px;
    margin-top: 20px;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    line-height: 1.3;
}
.hospitaldetail__hospital-review-area__container__content__create-review-box
    input,
.hospitaldetail__hospital-review-area__container__content__create-review-box
    textarea {
    margin: 10px;
}

@media screen and (max-width: 900px) {
    .hospitaldetail__hospital-review-area__container__content {
        flex-direction: column-reverse;
    }
    .hospitaldetail__hospital-review-area__container__content__create-review-box {
        padding: 20px 50px;
    }
}

.hospitaldetail__loader {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Glory", sans-serif;
}

.parent_home_container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 0;
    height: 100vh;
    max-height: 1000px;
    width: 100%;
    background: url(../../static/media/home_bg.6772c2ec.png) center center no-repeat;
    background-size: cover;
    background-size: cover;
    -o-background-size: cover;
}
.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
}

#home {
    z-index: 1;
    position: absolute;
}

#home h1 {
    font-size: 4rem;
    font-weight: 900;
    color: ivory;
}

#home h2 {
    margin-top: 1rem;
    margin-bottom: 3rem;
    font-size: 3rem;
    font-weight: 900;
    color: ivory;
}

.home_info{
    color: black ;
}

.home_info p{
    margin: 2.5rem 3rem;
    font-size: 1.8rem;
    text-align: left;   
}


.home_info h3{
    margin: 1rem 3rem;
    font-size: 1.8rem;
    text-align: left;
}

.home_info li{
    margin: 0 5rem;
    list-style: circle;
    text-align: left;
    font-size: 1.3rem;
    margin-top: 1rem;
}


@media only screen and (max-width: 720px){


    /* //////////////////// Home CSS ///////////////////// */

    .parent_home_container {
        height: 80vh;
    }

    #home h1{
        font-size: 2.1rem;        
        margin-top: 3rem;        
    }
    
    #home h2{
        margin: 0 1rem;
        margin-top: 1rem;
        margin-bottom: 3rem;
        font-size: 1.8rem;
    }

    .home_info p{
        margin: 2rem 2rem;
        font-size: 1.3rem;
    }
    
    
    .home_info h3{
        margin: 1rem 2rem;
        font-size: 1.3rem;
    }
    
    .home_info li{
        margin: 0 2rem;
        font-size: 1rem;
        margin-top: 0.5rem;
    }

}


@media only screen and (max-width: 380px){

    #home h1{
        font-size: 1.8rem;        
        margin-top: 1rem;        
    }
    
    #home h2{
        margin: 0 1rem;
        margin-top: 1rem;
        margin-bottom: 3rem;
        font-size: 1.5rem;
    }

}
.title{
    margin-top: 2rem;
}

hr{
    margin: 0 3rem;
}

.dots{
    border: 0;
    border-top: 0.4rem dotted black;
    margin: 1rem 45%;
}

.about_parent_container{
    display: grid;
    grid-template-columns: auto auto;
}

.myself{
    padding: 2rem;
    text-align: center;
    margin: 2rem 1rem;
}


.myself h1{
    font-size: 2.5rem;    
}

.myself hr{
    margin-bottom: 1.5rem;
    margin-top: 1.5rem;
}

.myself p{
    font-size: 1.4rem;
    line-height: 2rem;
}

.about_member{
    display: flex;
    justify-content: center;
}


@media only screen and (max-width: 560px){

    .dots{
        border-top: 0.3rem dotted black;
    }

    .about_parent_container{
        display: grid;
        grid-template-columns: auto;
    }

    .myself{
        padding: 1rem;
        margin: 1rem 1rem;
    }

    .myself h1{
        font-size: 1.5rem;    
    }

    .myself hr{
        margin-bottom: 1rem;
        margin-top: 1rem;
    }
    
    .myself p{
        font-size: 1rem;
        line-height: 1.5rem;
    }

}
